import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import VoiceRecognition from '../components/VoiceRecognition';
import VoiceAnalysis from '../components/VoiceAnalysis';
import llmService from '../services/llmService';
import './ReviewGenerator.css';

const ReviewGenerator = () => {
    const [reviewData, setReviewData] = useState({
        reviewType: 'restaurant',
        rating: 5,
        tone: 'professional',
        pros: '',
        cons: '',
        experience: ''
    });
    const [generatedReview, setGeneratedReview] = useState('');
    const [isGenerating, setIsGenerating] = useState(false);
    const [analysis, setAnalysis] = useState(null);
    const [isAnalyzing, setIsAnalyzing] = useState(false);
    const [showAnalysis, setShowAnalysis] = useState(false);
    const history = useHistory();

    const reviewTypes = [
        { value: 'restaurant', label: 'Restaurant', icon: 'ğŸ½ï¸' },
        { value: 'hotel', label: 'Hotel', icon: 'ğŸ¨' },
        { value: 'product', label: 'Product', icon: 'ğŸ“±' },
        { value: 'service', label: 'Service', icon: 'ğŸ› ï¸' },
        { value: 'experience', label: 'Experience', icon: 'ğŸ¯' },
        { value: 'app', label: 'App/Software', icon: 'ğŸ’»' },
        { value: 'place', label: 'Place/Location', icon: 'ğŸ“' },
        { value: 'general', label: 'General', icon: 'ğŸ“' }
    ];

    const tones = [
        { value: 'professional', label: 'Professional', icon: 'ğŸ‘”' },
        { value: 'casual', label: 'Casual', icon: 'ğŸ˜Š' },
        { value: 'enthusiastic', label: 'Enthusiastic', icon: 'ğŸ‰' },
        { value: 'critical', label: 'Critical', icon: 'ğŸ¤”' },
        { value: 'balanced', label: 'Balanced', icon: 'âš–ï¸' }
    ];

    const handleInputChange = (field, value) => {
        setReviewData(prev => ({
            ...prev,
            [field]: value
        }));
    };

    const handleVoiceTranscript = (field) => (transcript) => {
        setReviewData(prev => ({
            ...prev,
            [field]: transcript
        }));
    };

    const generateReview = () => {
        setIsGenerating(true);
        
        setTimeout(() => {
            const review = createReview(reviewData);
            setGeneratedReview(review);
            setIsGenerating(false);
            
            // Save review to localStorage for history
            const reviewHistory = JSON.parse(localStorage.getItem('reviewHistory') || '[]');
            const newReview = {
                id: Date.now(),
                reviewType: reviewData.reviewType,
                itemName: `${reviewData.reviewType.charAt(0).toUpperCase() + reviewData.reviewType.slice(1)} Review`,
                category: reviewData.reviewType,
                rating: reviewData.rating,
                tone: reviewData.tone,
                pros: reviewData.pros,
                cons: reviewData.cons,
                experience: reviewData.experience,
                review: review,
                timestamp: new Date().toISOString()
            };
            reviewHistory.unshift(newReview);
            localStorage.setItem('reviewHistory', JSON.stringify(reviewHistory.slice(0, 50))); // Keep last 50
            
            // Auto-analyze the generated review
            setTimeout(() => {
                analyzeReview(review);
            }, 500);
        }, 2000);
    };

    const analyzeReview = async (reviewText) => {
        if (!reviewText || reviewText.trim().length === 0) {
            return;
        }

        setIsAnalyzing(true);
        try {
            const result = await llmService.analyzeVoiceInput(reviewText, {
                reviewType: reviewData.reviewType,
                rating: reviewData.rating,
                tone: reviewData.tone,
                isGeneratedReview: true,
                timestamp: new Date().toISOString()
            });
            
            setAnalysis(result);
            setShowAnalysis(true);
            console.log('Review analysis result:', result);
        } catch (error) {
            console.error('Review analysis failed:', error);
            setAnalysis({
                success: false,
                error: error.message || 'Analysis failed'
            });
        } finally {
            setIsAnalyzing(false);
        }
    };

    const createReview = (data) => {
        const { reviewType, rating, tone, pros, cons, experience } = data;
        
        let review = '';
        const stars = 'â­'.repeat(rating) + 'â˜†'.repeat(5 - rating);
        
        const toneWords = {
            professional: { positive: 'excellent', negative: 'disappointing', neutral: 'satisfactory' },
            casual: { positive: 'awesome', negative: 'not great', neutral: 'okay' },
            enthusiastic: { positive: 'amazing', negative: 'terrible', neutral: 'decent' },
            critical: { positive: 'adequate', negative: 'poor', neutral: 'acceptable' },
            balanced: { positive: 'good', negative: 'lacking', neutral: 'fair' }
        };
        
        const words = toneWords[tone];
        
        review = `${stars}\n\n${reviewType.charAt(0).toUpperCase() + reviewType.slice(1)} Review\n\n`;
        
        if (rating >= 4) {
            review += `This ${reviewType} is ${words.positive}. `;
        } else if (rating <= 2) {
            review += `This ${reviewType} was ${words.negative}. `;
        } else {
            review += `This ${reviewType} is ${words.neutral}. `;
        }
        
        if (pros) {
            review += `\n\nWhat I liked:\n${pros}`;
        }
        
        if (cons) {
            review += `\n\nWhat could be improved:\n${cons}`;
        }
        
        if (experience) {
            review += `\n\nMy experience:\n${experience}`;
        }
        
        if (rating >= 4) {
            review += `\n\nI would definitely recommend this to others!`;
        } else if (rating <= 2) {
            review += `\n\nI would not recommend this.`;
        } else {
            review += `\n\nIt's worth considering if you're interested.`;
        }
        
        return review;
    };

    const copyToClipboard = () => {
        navigator.clipboard.writeText(generatedReview);
        alert('Review copied to clipboard!');
    };

    const resetReview = () => {
        setReviewData({
            reviewType: 'restaurant',
            rating: 5,
            tone: 'professional',
            pros: '',
            cons: '',
            experience: ''
        });
        setGeneratedReview('');
        setAnalysis(null);
        setShowAnalysis(false);
    };

    const handleGenerateReviewFromAnalysis = async (transcript, analysisData) => {
        try {
            const result = await llmService.generateReviewFromVoice(
                transcript, 
                reviewData.reviewType, 
                reviewData.rating
            );
            
            if (result.success) {
                setGeneratedReview(result.reviewText);
            }
        } catch (error) {
            console.error('Review generation failed:', error);
        }
    };

    const handleSaveAnalysis = (analysisData) => {
        const analysisRecord = {
            id: Date.now(),
            analysis: analysisData,
            reviewData: reviewData,
            timestamp: new Date().toISOString()
        };
        
        // Save to localStorage
        const existingAnalyses = JSON.parse(localStorage.getItem('reviewAnalyses') || '[]');
        localStorage.setItem('reviewAnalyses', JSON.stringify([analysisRecord, ...existingAnalyses]));
        
        alert('Analysis saved successfully!');
    };

    return (
        <div className="review-generator">
            <div className="review-container">
                <div className="review-header">
                    <h1>ğŸ“ AI-Powered Review Generator</h1>
                    <p>Create professional reviews with AI analysis and insights</p>
                </div>

                {!generatedReview ? (
                    <div className="review-form">
                        <div className="form-section">
                            <h2>ğŸ¯ Review Settings</h2>
                            <div className="settings-grid">
                                <div className="setting-group">
                                    <label>Review Type:</label>
                                    <div className="type-options">
                                        {reviewTypes.map(type => (
                                            <button
                                                key={type.value}
                                                className={`type-option ${reviewData.reviewType === type.value ? 'active' : ''}`}
                                                onClick={() => handleInputChange('reviewType', type.value)}
                                            >
                                                <span className="type-icon">{type.icon}</span>
                                                <span>{type.label}</span>
                                            </button>
                                        ))}
                                    </div>
                                </div>

                                <div className="setting-group">
                                    <label>Rating: {reviewData.rating}/5</label>
                                    <div className="rating-input">
                                        {[1, 2, 3, 4, 5].map(star => (
                                            <button
                                                key={star}
                                                className={`star-btn ${star <= reviewData.rating ? 'active' : ''}`}
                                                onClick={() => handleInputChange('rating', star)}
                                            >
                                                â­
                                            </button>
                                        ))}
                                    </div>
                                </div>

                                <div className="setting-group">
                                    <label>Tone:</label>
                                    <div className="tone-options">
                                        {tones.map(tone => (
                                            <button
                                                key={tone.value}
                                                className={`tone-option ${reviewData.tone === tone.value ? 'active' : ''}`}
                                                onClick={() => handleInputChange('tone', tone.value)}
                                            >
                                                <span className="tone-icon">{tone.icon}</span>
                                                <span>{tone.label}</span>
                                            </button>
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="form-section">
                            <h2>âœï¸ Review Content</h2>
                            <div className="content-grid">
                                <div className="content-group">
                                    <label>What did you like? (Pros)</label>
                                    <div className="input-with-voice">
                                        <textarea
                                            value={reviewData.pros}
                                            onChange={(e) => handleInputChange('pros', e.target.value)}
                                            placeholder="Describe what you liked about this experience..."
                                            rows="3"
                                        />
                                        <VoiceRecognition
                                            onTranscript={handleVoiceTranscript('pros')}
                                            placeholder="Speak about the positive aspects..."
                                        />
                                    </div>
                                </div>

                                <div className="content-group">
                                    <label>What could be improved? (Cons)</label>
                                    <div className="input-with-voice">
                                        <textarea
                                            value={reviewData.cons}
                                            onChange={(e) => handleInputChange('cons', e.target.value)}
                                            placeholder="Describe areas for improvement..."
                                            rows="3"
                                        />
                                        <VoiceRecognition
                                            onTranscript={handleVoiceTranscript('cons')}
                                            placeholder="Speak about areas for improvement..."
                                        />
                                    </div>
                                </div>

                                <div className="content-group full-width">
                                    <label>Additional Experience Details</label>
                                    <div className="input-with-voice">
                                        <textarea
                                            value={reviewData.experience}
                                            onChange={(e) => handleInputChange('experience', e.target.value)}
                                            placeholder="Share your overall experience and thoughts..."
                                            rows="4"
                                        />
                                        <VoiceRecognition
                                            onTranscript={handleVoiceTranscript('experience')}
                                            placeholder="Speak about your overall experience..."
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="form-actions">
                            <button
                                className="generate-btn"
                                onClick={generateReview}
                                disabled={isGenerating}
                            >
                                {isGenerating ? (
                                    <>
                                        <span className="loading-spinner"></span>
                                        Generating Review...
                                    </>
                                ) : (
                                    <>
                                        âœ¨ Generate AI Review
                                    </>
                                )}
                            </button>
                            <button className="reset-btn" onClick={resetReview}>
                                ğŸ”„ Reset
                            </button>
                        </div>
                        
                        <div className="form-hint">
                            <p>ğŸ’¡ <strong>Tip:</strong> Fill in at least one field (pros, cons, or experience) for a better review. You can use voice input for any field!</p>
                        </div>
                    </div>
                ) : (
                    <div className="result-section">
                        <div className="result-header">
                            <h2>ğŸ‰ Your AI Review is Ready!</h2>
                            <div className="result-actions">
                                <button onClick={copyToClipboard} className="copy-btn">
                                    ğŸ“‹ Copy Review
                                </button>
                                <button 
                                    onClick={() => setShowAnalysis(!showAnalysis)} 
                                    className="analysis-toggle-btn"
                                >
                                    {showAnalysis ? 'ğŸ”½ Hide Analysis' : 'ğŸ§  Show AI Analysis'}
                                </button>
                                <button onClick={resetReview} className="reset-btn">
                                    ğŸ¤ Create Another Review
                                </button>
                            </div>
                        </div>

                        <div className="review-content">
                            <pre>{generatedReview}</pre>
                        </div>

                        {isAnalyzing && (
                            <div className="analyzing-indicator">
                                <div className="loading-spinner"></div>
                                <p>ğŸ¤– AI is analyzing your review...</p>
                            </div>
                        )}

                        {showAnalysis && analysis && (
                            <div className="analysis-section">
                                <h3>ğŸ§  AI Analysis of Your Review</h3>
                                <VoiceAnalysis
                                    analysis={analysis}
                                    onGenerateReview={handleGenerateReviewFromAnalysis}
                                    onSaveAnalysis={handleSaveAnalysis}
                                />
                            </div>
                        )}
                    </div>
                )}
            </div>
        </div>
    );
};

export default ReviewGenerator; 