import React, { useState, useEffect, useRef } from 'react';
import './VoiceRecognition.css';

const VoiceRecognition = ({ onTranscript, placeholder = "Click to start speaking...", disabled = false }) => {
    const [isListening, setIsListening] = useState(false);
    const [transcript, setTranscript] = useState('');
    const [interimTranscript, setInterimTranscript] = useState('');
    const [error, setError] = useState('');
    const [isSupported, setIsSupported] = useState(false);
    const [isInitialized, setIsInitialized] = useState(false);
    const recognitionRef = useRef(null);

    useEffect(() => {
        initializeSpeechRecognition();
        
        return () => {
            if (recognitionRef.current) {
                try {
                    recognitionRef.current.abort();
                } catch (e) {
                    console.log('Cleanup error:', e);
                }
            }
        };
    }, []);

    const initializeSpeechRecognition = () => {
        try {
            // Check for speech recognition support
            const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
            
            if (!SpeechRecognition) {
                setError('Speech recognition is not supported in this browser. Please use Chrome, Edge, or Safari.');
                setIsSupported(false);
                return;
            }

            // Create recognition instance
            recognitionRef.current = new SpeechRecognition();
            setupRecognition();
            setIsSupported(true);
            setIsInitialized(true);
            
        } catch (error) {
            console.error('Failed to initialize speech recognition:', error);
            setError('Failed to initialize speech recognition. Please refresh the page and try again.');
            setIsSupported(false);
        }
    };

    const setupRecognition = () => {
        const recognition = recognitionRef.current;
        
        if (!recognition) return;

        // Configure recognition settings
        recognition.continuous = true;
        recognition.interimResults = true;
        recognition.lang = 'en-US';
        recognition.maxAlternatives = 1;

        // Event handlers
        recognition.onstart = () => {
            console.log('Speech recognition started');
            setIsListening(true);
            setError('');
        };

        recognition.onresult = (event) => {
            let finalTranscript = '';
            let interimTranscript = '';

            try {
                for (let i = event.resultIndex; i < event.results.length; i++) {
                    const transcript = event.results[i][0].transcript;
                    if (event.results[i].isFinal) {
                        finalTranscript += transcript;
                    } else {
                        interimTranscript += transcript;
                    }
                }

                console.log('Voice recognition result:', { finalTranscript, interimTranscript });

                if (finalTranscript) {
                    const newTranscript = transcript + finalTranscript;
                    setTranscript(newTranscript);
                    setInterimTranscript('');
                    
                    if (onTranscript) {
                        console.log('Calling onTranscript with:', newTranscript);
                        onTranscript(newTranscript);
                    }
                } else {
                    setInterimTranscript(interimTranscript);
                }
            } catch (error) {
                console.error('Error processing speech recognition result:', error);
                setError('Error processing speech. Please try again.');
            }
        };

        recognition.onerror = (event) => {
            console.error('Speech recognition error:', event.error);
            setIsListening(false);
            
            let errorMessage = '';
            switch (event.error) {
                case 'no-speech':
                    errorMessage = 'No speech detected. Please try speaking again.';
                    break;
                case 'audio-capture':
                    errorMessage = 'Audio capture failed. Please check your microphone permissions and try again.';
                    break;
                case 'not-allowed':
                    errorMessage = 'Microphone access denied. Please allow microphone access in your browser settings and refresh the page.';
                    break;
                case 'network':
                    errorMessage = 'Network error. Please check your internet connection and try again.';
                    break;
                case 'service-not-allowed':
                    errorMessage = 'Speech recognition service not allowed. Please try a different browser.';
                    break;
                case 'bad-grammar':
                    errorMessage = 'Speech recognition grammar error. Please try speaking more clearly.';
                    break;
                case 'language-not-supported':
                    errorMessage = 'Language not supported. Please try speaking in English.';
                    break;
                default:
                    errorMessage = `Speech recognition error: ${event.error}. Please try again.`;
            }
            
            setError(errorMessage);
        };

        recognition.onend = () => {
            console.log('Speech recognition ended');
            setIsListening(false);
        };

        recognition.onaudiostart = () => {
            console.log('Audio capturing started');
        };

        recognition.onaudioend = () => {
            console.log('Audio capturing ended');
        };

        recognition.onsoundstart = () => {
            console.log('Sound detected');
        };

        recognition.onsoundend = () => {
            console.log('Sound ended');
        };

        recognition.onspeechstart = () => {
            console.log('Speech started');
        };

        recognition.onspeechend = () => {
            console.log('Speech ended');
        };
    };

    const startListening = () => {
        if (!isSupported || disabled || !isInitialized) {
            console.log('Cannot start listening:', { isSupported, disabled, isInitialized });
            return;
        }
        
        try {
            setError('');
            console.log('Starting speech recognition...');
            recognitionRef.current.start();
        } catch (error) {
            console.error('Error starting speech recognition:', error);
            setError('Failed to start speech recognition. Please try again.');
            
            // Try to reinitialize if there's an error
            setTimeout(() => {
                console.log('Attempting to reinitialize speech recognition...');
                initializeSpeechRecognition();
            }, 1000);
        }
    };

    const stopListening = () => {
        if (recognitionRef.current && isListening) {
            try {
                console.log('Stopping speech recognition...');
                recognitionRef.current.stop();
            } catch (error) {
                console.error('Error stopping speech recognition:', error);
            }
        }
    };

    const clearTranscript = () => {
        setTranscript('');
        setInterimTranscript('');
        if (onTranscript) {
            onTranscript('');
        }
    };

    const copyToClipboard = () => {
        const textToCopy = transcript + interimTranscript;
        if (textToCopy) {
            try {
                navigator.clipboard.writeText(textToCopy);
                const originalError = error;
                setError('Copied to clipboard!');
                setTimeout(() => setError(originalError), 2000);
            } catch (error) {
                console.error('Failed to copy to clipboard:', error);
                setError('Failed to copy to clipboard. Please select and copy manually.');
            }
        }
    };

    const retryInitialization = () => {
        setError('');
        setIsInitialized(false);
        setTimeout(() => {
            initializeSpeechRecognition();
        }, 500);
    };

    if (!isSupported) {
        return (
            <div className="voice-recognition-container">
                <div className="voice-error">
                    <div className="error-icon">ğŸ¤</div>
                    <p>{error}</p>
                    <p className="error-suggestion">
                        Try using Chrome, Edge, or Safari for voice recognition support.
                    </p>
                    <button onClick={retryInitialization} className="retry-btn">
                        ğŸ”„ Retry
                    </button>
                </div>
            </div>
        );
    }

    return (
        <div className="voice-recognition-container">
            <div className="voice-controls">
                <button
                    className={`voice-button ${isListening ? 'listening' : ''} ${disabled ? 'disabled' : ''}`}
                    onClick={isListening ? stopListening : startListening}
                    disabled={disabled || !isInitialized}
                    title={isListening ? 'Stop recording' : 'Start recording'}
                >
                    <div className="voice-icon">
                        {isListening ? 'â¹ï¸' : 'ğŸ¤'}
                    </div>
                    <div className="voice-status">
                        {isListening ? 'Listening...' : 'Start Voice Input'}
                    </div>
                    {isListening && (
                        <div className="pulse-animation"></div>
                    )}
                </button>

                {(transcript || interimTranscript) && (
                    <div className="voice-actions">
                        <button
                            className="action-btn clear-btn"
                            onClick={clearTranscript}
                            title="Clear transcript"
                        >
                            ğŸ—‘ï¸ Clear
                        </button>
                        <button
                            className="action-btn copy-btn"
                            onClick={copyToClipboard}
                            title="Copy to clipboard"
                        >
                            ğŸ“‹ Copy
                        </button>
                    </div>
                )}
            </div>

            <div className="transcript-container">
                {transcript || interimTranscript ? (
                    <div className="transcript-content">
                        <div className="transcript-text">
                            {transcript}
                            {interimTranscript && (
                                <span className="interim-text">{interimTranscript}</span>
                            )}
                        </div>
                        <div className="transcript-stats">
                            <span className="word-count">
                                {(transcript + interimTranscript).split(' ').length} words
                            </span>
                            <span className="char-count">
                                {(transcript + interimTranscript).length} characters
                            </span>
                        </div>
                    </div>
                ) : (
                    <div className="transcript-placeholder">
                        <div className="placeholder-icon">ğŸ’¬</div>
                        <p>{placeholder}</p>
                        <p className="placeholder-hint">
                            Click the microphone button above to start speaking
                        </p>
                    </div>
                )}
            </div>

            {error && (
                <div className="voice-error">
                    <div className="error-icon">âš ï¸</div>
                    <p>{error}</p>
                    {error.includes('microphone') && (
                        <button onClick={retryInitialization} className="retry-btn">
                            ğŸ”„ Retry
                        </button>
                    )}
                </div>
            )}

            {isListening && (
                <div className="listening-indicator">
                    <div className="listening-dots">
                        <span></span>
                        <span></span>
                        <span></span>
                    </div>
                    <p>Listening... Speak now</p>
                </div>
            )}

            {!isInitialized && isSupported && (
                <div className="initializing-indicator">
                    <div className="loading-spinner"></div>
                    <p>Initializing voice recognition...</p>
                </div>
            )}
        </div>
    );
};

export default VoiceRecognition; 