import React, { useState } from 'react';
import './VoiceAnalysis.css';

const VoiceAnalysis = ({ analysis, onGenerateReview, onSaveAnalysis }) => {
    const [isExpanded, setIsExpanded] = useState(false);
    const [isGeneratingReview, setIsGeneratingReview] = useState(false);

    if (!analysis || !analysis.success) {
        return (
            <div className="voice-analysis-container">
                <div className="analysis-error">
                    <div className="error-icon">âš ï¸</div>
                    <h3>Analysis Failed</h3>
                    <p>{analysis?.error || 'Unable to analyze voice input'}</p>
                </div>
            </div>
        );
    }

    const { analysis: data, provider } = analysis;
    const {
        sentiment,
        confidence,
        keyPoints,
        topics,
        suggestions,
        tone,
        actionItems,
        summary,
        wordCount,
        speakingPace,
        transcript
    } = data;

    const getSentimentColor = (sentiment) => {
        switch (sentiment) {
            case 'positive': return '#28a745';
            case 'negative': return '#dc3545';
            default: return '#6c757d';
        }
    };

    const getConfidenceColor = (confidence) => {
        if (confidence >= 0.8) return '#28a745';
        if (confidence >= 0.6) return '#ffc107';
        return '#dc3545';
    };

    const handleGenerateReview = async () => {
        if (onGenerateReview) {
            setIsGeneratingReview(true);
            try {
                await onGenerateReview(transcript, data);
            } finally {
                setIsGeneratingReview(false);
            }
        }
    };

    const handleSaveAnalysis = () => {
        if (onSaveAnalysis) {
            onSaveAnalysis(analysis);
        }
    };

    return (
        <div className="voice-analysis-container">
            <div className="analysis-header">
                <div className="provider-badge">
                    <span className="provider-icon">ğŸ¤–</span>
                    {provider}
                </div>
                <div className="analysis-actions">
                    <button 
                        className="action-btn expand-btn"
                        onClick={() => setIsExpanded(!isExpanded)}
                        title={isExpanded ? 'Collapse' : 'Expand'}
                    >
                        {isExpanded ? 'ğŸ“' : 'ğŸ“‚'}
                    </button>
                    <button 
                        className="action-btn save-btn"
                        onClick={handleSaveAnalysis}
                        title="Save Analysis"
                    >
                        ğŸ’¾
                    </button>
                </div>
            </div>

            <div className="analysis-summary">
                <div className="sentiment-section">
                    <div className="sentiment-indicator" style={{ backgroundColor: getSentimentColor(sentiment) }}>
                        <span className="sentiment-emoji">
                            {sentiment === 'positive' ? 'ğŸ˜Š' : sentiment === 'negative' ? 'ğŸ˜' : 'ğŸ˜'}
                        </span>
                        <span className="sentiment-text">{sentiment.toUpperCase()}</span>
                    </div>
                    <div className="confidence-meter">
                        <div className="confidence-label">Confidence</div>
                        <div className="confidence-bar">
                            <div 
                                className="confidence-fill" 
                                style={{ 
                                    width: `${confidence * 100}%`,
                                    backgroundColor: getConfidenceColor(confidence)
                                }}
                            ></div>
                        </div>
                        <div className="confidence-value">{Math.round(confidence * 100)}%</div>
                    </div>
                </div>

                <div className="stats-grid">
                    <div className="stat-item">
                        <div className="stat-label">Words</div>
                        <div className="stat-value">{wordCount}</div>
                    </div>
                    <div className="stat-item">
                        <div className="stat-label">Pace</div>
                        <div className="stat-value">{speakingPace}</div>
                    </div>
                    <div className="stat-item">
                        <div className="stat-label">Tone</div>
                        <div className="stat-value">{tone}</div>
                    </div>
                    <div className="stat-item">
                        <div className="stat-label">Topics</div>
                        <div className="stat-value">{topics?.length || 0}</div>
                    </div>
                </div>
            </div>

            <div className="analysis-content">
                <div className="content-section">
                    <h3>ğŸ“ Summary</h3>
                    <p className="summary-text">{summary}</p>
                </div>

                {keyPoints && keyPoints.length > 0 && (
                    <div className="content-section">
                        <h3>ğŸ¯ Key Points</h3>
                        <ul className="key-points-list">
                            {keyPoints.map((point, index) => (
                                <li key={index} className="key-point-item">
                                    <span className="point-bullet">â€¢</span>
                                    {point}
                                </li>
                            ))}
                        </ul>
                    </div>
                )}

                {topics && topics.length > 0 && (
                    <div className="content-section">
                        <h3>ğŸ·ï¸ Topics</h3>
                        <div className="topics-tags">
                            {topics.map((topic, index) => (
                                <span key={index} className="topic-tag">
                                    {topic}
                                </span>
                            ))}
                        </div>
                    </div>
                )}

                {isExpanded && (
                    <>
                        {suggestions && suggestions.length > 0 && (
                            <div className="content-section">
                                <h3>ğŸ’¡ Suggestions</h3>
                                <ul className="suggestions-list">
                                    {suggestions.map((suggestion, index) => (
                                        <li key={index} className="suggestion-item">
                                            <span className="suggestion-icon">ğŸ’¡</span>
                                            {suggestion}
                                        </li>
                                    ))}
                                </ul>
                            </div>
                        )}

                        {actionItems && actionItems.length > 0 && (
                            <div className="content-section">
                                <h3>âœ… Action Items</h3>
                                <ul className="action-items-list">
                                    {actionItems.map((item, index) => (
                                        <li key={index} className="action-item">
                                            <span className="action-icon">âœ…</span>
                                            {item}
                                        </li>
                                    ))}
                                </ul>
                            </div>
                        )}

                        <div className="content-section">
                            <h3>ğŸ¤ Original Transcript</h3>
                            <div className="transcript-display">
                                <p>{transcript}</p>
                            </div>
                        </div>
                    </>
                )}
            </div>

            <div className="analysis-actions-footer">
                <button 
                    className="generate-review-btn"
                    onClick={handleGenerateReview}
                    disabled={isGeneratingReview}
                >
                    {isGeneratingReview ? (
                        <>
                            <span className="loading-spinner"></span>
                            Generating Review...
                        </>
                    ) : (
                        <>
                            âœ¨ Generate Review
                        </>
                    )}
                </button>
            </div>
        </div>
    );
};

export default VoiceAnalysis; 