import React, { useState } from 'react';
import VoiceRecognition from '../components/VoiceRecognition';
import VoiceAnalysis from '../components/VoiceAnalysis';
import llmService from '../services/llmService';
import './VoiceReview.css';

const VoiceReview = () => {
    const [transcript, setTranscript] = useState('');
    const [analysis, setAnalysis] = useState(null);
    const [isAnalyzing, setIsAnalyzing] = useState(false);
    const [reviewType, setReviewType] = useState('restaurant');
    const [rating, setRating] = useState(5);
    const [generatedReview, setGeneratedReview] = useState('');
    const [savedReviews, setSavedReviews] = useState([]);

    const handleTranscript = (newTranscript) => {
        setTranscript(newTranscript);
        // Auto-analyze when transcript changes significantly
        if (newTranscript.length > 20 && !isAnalyzing) {
            setTimeout(() => {
                analyzeVoiceInput(newTranscript);
            }, 1000);
        }
    };

    const analyzeVoiceInput = async (inputTranscript) => {
        if (!inputTranscript || inputTranscript.trim().length === 0) {
            return;
        }

        setIsAnalyzing(true);
        try {
            const result = await llmService.analyzeVoiceInput(inputTranscript, {
                reviewType,
                rating,
                timestamp: new Date().toISOString()
            });
            
            setAnalysis(result);
            console.log('Voice analysis result:', result);
        } catch (error) {
            console.error('Analysis failed:', error);
            setAnalysis({
                success: false,
                error: error.message || 'Analysis failed'
            });
        } finally {
            setIsAnalyzing(false);
        }
    };

    const handleGenerateReview = async (inputTranscript, analysisData) => {
        try {
            const result = await llmService.generateReviewFromVoice(
                inputTranscript, 
                reviewType, 
                rating
            );
            
            if (result.success) {
                setGeneratedReview(result.reviewText);
                // Auto-save the generated review
                const reviewData = {
                    id: Date.now(),
                    type: reviewType,
                    rating,
                    review: result.reviewText,
                    transcript: inputTranscript,
                    analysis: analysisData,
                    timestamp: new Date().toISOString()
                };
                setSavedReviews(prev => [reviewData, ...prev]);
                
                // Save to localStorage
                const existingReviews = JSON.parse(localStorage.getItem('voiceReviews') || '[]');
                localStorage.setItem('voiceReviews', JSON.stringify([reviewData, ...existingReviews]));
            }
        } catch (error) {
            console.error('Review generation failed:', error);
        }
    };

    const handleSaveAnalysis = (analysisData) => {
        const analysisRecord = {
            id: Date.now(),
            analysis: analysisData,
            timestamp: new Date().toISOString()
        };
        
        // Save to localStorage
        const existingAnalyses = JSON.parse(localStorage.getItem('voiceAnalyses') || '[]');
        localStorage.setItem('voiceAnalyses', JSON.stringify([analysisRecord, ...existingAnalyses]));
        
        alert('Analysis saved successfully!');
    };

    const clearAll = () => {
        setTranscript('');
        setAnalysis(null);
        setGeneratedReview('');
    };

    return (
        <div className="voice-review">
            <div className="voice-review-container">
                <div className="voice-review-header">
                    <h1>ğŸ¤ AI Voice Review Generator</h1>
                    <p>Speak your experience and let AI analyze and generate a professional review</p>
                </div>

                <div className="voice-review-content">
                    <div className="settings-panel">
                        <div className="setting-group">
                            <label htmlFor="reviewType">Review Type:</label>
                            <select 
                                id="reviewType"
                                value={reviewType} 
                                onChange={(e) => setReviewType(e.target.value)}
                                className="setting-input"
                            >
                                <option value="restaurant">Restaurant</option>
                                <option value="hotel">Hotel</option>
                                <option value="product">Product</option>
                                <option value="service">Service</option>
                                <option value="experience">Experience</option>
                                <option value="app">App/Software</option>
                                <option value="place">Place/Location</option>
                                <option value="general">General</option>
                            </select>
                        </div>

                        <div className="setting-group">
                            <label htmlFor="rating">Rating:</label>
                            <div className="rating-input">
                                {[1, 2, 3, 4, 5].map(star => (
                                    <button
                                        key={star}
                                        className={`star-btn ${star <= rating ? 'active' : ''}`}
                                        onClick={() => setRating(star)}
                                    >
                                        â­
                                    </button>
                                ))}
                                <span className="rating-text">{rating}/5</span>
                            </div>
                        </div>
                    </div>

                    <div className="voice-input-section">
                        <h2>ğŸ¤ Voice Input</h2>
                        <VoiceRecognition
                            onTranscript={handleTranscript}
                            placeholder="Click the microphone and speak about your experience..."
                        />
                        
                        {transcript && (
                            <div className="transcript-display">
                                <h3>Your Voice Input:</h3>
                                <p>{transcript}</p>
                                <div className="transcript-actions">
                                    <button onClick={() => analyzeVoiceInput(transcript)} className="analyze-btn">
                                        ğŸ” Analyze Voice
                                    </button>
                                    <button onClick={clearAll} className="clear-btn">
                                        ğŸ—‘ï¸ Clear All
                                    </button>
                                </div>
                            </div>
                        )}
                    </div>

                    {isAnalyzing && (
                        <div className="analyzing-indicator">
                            <div className="loading-spinner"></div>
                            <p>ğŸ¤– AI is analyzing your voice input...</p>
                        </div>
                    )}

                    {analysis && (
                        <div className="analysis-section">
                            <h2>ğŸ§  AI Analysis</h2>
                            <VoiceAnalysis
                                analysis={analysis}
                                onGenerateReview={handleGenerateReview}
                                onSaveAnalysis={handleSaveAnalysis}
                            />
                        </div>
                    )}

                    {generatedReview && (
                        <div className="generated-review-section">
                            <h2>âœ¨ Generated Review</h2>
                            <div className="generated-review">
                                <div className="review-content">
                                    <pre>{generatedReview}</pre>
                                </div>
                                <div className="review-actions">
                                    <button 
                                        onClick={() => navigator.clipboard.writeText(generatedReview)}
                                        className="copy-btn"
                                    >
                                        ğŸ“‹ Copy Review
                                    </button>
                                    <button 
                                        onClick={() => setGeneratedReview('')}
                                        className="clear-btn"
                                    >
                                        ğŸ—‘ï¸ Clear
                                    </button>
                                </div>
                            </div>
                        </div>
                    )}

                    {savedReviews.length > 0 && (
                        <div className="saved-reviews-section">
                            <h2>ğŸ’¾ Saved Reviews</h2>
                            <div className="saved-reviews">
                                {savedReviews.slice(0, 3).map(review => (
                                    <div key={review.id} className="saved-review-item">
                                        <div className="review-header">
                                            <span className="review-type">{review.type}</span>
                                            <span className="review-rating">{'â­'.repeat(review.rating)}</span>
                                            <span className="review-date">
                                                {new Date(review.timestamp).toLocaleDateString()}
                                            </span>
                                        </div>
                                        <p className="review-text">{review.review}</p>
                                    </div>
                                ))}
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

export default VoiceReview; 